<template>
  <div style="width:100%; height:100%; display:block; position:fixed">
    <div id="login_wrapper">
      <div class="login_logo2"></div>
      <div class="login_box">
        <div class="login_box_inner">
          <h1>로그인</h1>
          <div><input type="text" class="login_input" v-model="member.id" placeholder="아이디입력"></div>
          <div><input type="password" class="login_input" v-model="member.memberPw" placeholder="비밀번호입력"></div>
          <div class="login_txt"><span><input type="checkbox">아이디 저장하기</span><span><a @click="move('/searchId')">아이디</a>/<a @click="move('/searchPw')">비밀번호 찾기</a></span></div>
          <button class="btn_login" @click="submit">로그인</button>
          <div class="small_txt2">pc와 모바일에서 하나의 아이디로 이용하실 수 있습니다.</div>
          <div class="join_btn"><span><a @click="join">회원가입</a></span></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import '@/assets/js/jquery-1.9.1.min.js';
import '@/assets/js/ux_common.js';
export default {
  name: 'LoginDo',
  components: {},
  data() {
    return {
      loginFlag: this.$route.query.loginFlag ? this.$route.query.loginFlag : 'TYPE-1',
      member: {
        id: "",
        memberPw: ""
      }
    }
  },
  methods: {
    move(path){
      this.$router.push(path)
    },
    join() {
      if (this.loginFlag === 'TYPE-0') {
        this.$router.push({ path: '/joinCorp' })
      } else {
        this.$router.push({ path: '/joinSolo' })
      }
    },
    submit() {
      const parameter = {
        member: this.member,
        loginFlag: this.loginFlag
      }
      this.$store.dispatch("REQUEST_LOGIN", parameter)
    }
  }
}
</script>

